import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpResponse, HttpErrorResponse, HttpHeaders} from '@angular/common/http'
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UaeServicesService {

  constructor(private httpClient: HttpClient ) { }

  gethomePageRes(): Observable<any[]> {
    console.log('Request is sent! for home Page');
    let Url = 'http://127.0.0.1:8000/uae/home';
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    }
    return this.httpClient.get<any[]>(Url,httpOptions);
  }
}
