import { Component, OnInit } from '@angular/core';
import { UaeServicesService } from '../../services/uae-services.service';
import AOS from 'aos';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  resData:any;
  constructor(private restApiService: UaeServicesService) { }

  ngOnInit(): void {
    this.HomePageApi();
    AOS.init();
  }
  async HomePageApi()
  {
    this.resData  = await this.restApiService.gethomePageRes().toPromise();
    
    console.log("Response from server :"+this.resData.response);
    console.log("Api called at :"+this.resData.time);
  }
}
